import React from 'react'

// material-ui
import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// custom component
import SiteHeader from '../components/Headers/SiteHeader'

/**
 * NotFoundPage component
 * @param {Object} props - component properties
 * @param {Object} props.location - representing where the app is from a router perspective
 * @returns {*}
 * @constructor
 */
function NotFoundPage({ location }) {
  return (
    <SiteHeader title="Home of modern software development" location={location}>
      <Box clone pt={16} pr={2} pb={2} pl={2}>
        <Paper elevation={0}>
          <Typography variant="h5" color="error">
            {'You hit a page that does not exist'}
          </Typography>
        </Paper>
      </Box>
    </SiteHeader>
  )
}

export default NotFoundPage
